@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
body {
  margin: 0 auto;
  background-color: #ffffff;
  font-family: "Lato", Tahoma, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  min-width: 320px;
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.additional-products {
  background-color: #ffffff;
  padding: 25px 0 35px 0;
  position: relative;
}
@media (min-width: 768px) {
  .additional-products {
    padding-bottom: 77px;
    padding-top: 71px;
  }
}
@media (min-width: 1300px) {
  .additional-products {
    padding-bottom: 86px;
    padding-top: 76px;
  }
}
.additional-products::before {
  display: block;
  position: absolute;
  left: 0;
  content: "";
  background-color: #68b738;
  height: 2px;
  width: 100%;
}
@media (max-width: 767px) {
  .additional-products::before {
    top: 40px;
  }
}
@media (min-width: 768px) {
  .additional-products::before {
    top: 90px;
  }
}
@media (min-width: 1300px) {
  .additional-products::before {
    top: 98px;
  }
}
.additional-products__title {
  display: inline-block;
  position: relative;
  margin-top: 0;
  font-family: "Oswald", Tahoma, sans-serif;
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  color: #000000;
}
.additional-products__title span {
  position: relative;
  background-color: #ffffff;
  z-index: 1;
}
@media (max-width: 767px) {
  .additional-products__title span {
    padding-left: 20px;
    padding-right: 20px;
    margin-left: -20px;
  }
}
@media (min-width: 768px) {
  .additional-products__title span {
    padding-left: 40px;
    padding-right: 40px;
  }
}
@media (min-width: 1300px) {
  .additional-products__title span {
    margin-left: -40px;
  }
}
@media (max-width: 767px) {
  .additional-products__title {
    margin-bottom: 35px;
  }
}
@media (min-width: 768px) {
  .additional-products__title {
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 79px;
  }
}
@media (min-width: 1300px) {
  .additional-products__title {
    margin-bottom: 79px;
  }
}
.additional-products__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .additional-products__container {
    gap: 34px;
  }
}
@media (min-width: 768px) {
  .additional-products__container {
    gap: 68px;
  }
}
@media (min-width: 1300px) {
  .additional-products__container {
    flex-direction: row;
    gap: 80px;
  }
}
.additional-products__advertising-text {
  display: block;
  margin: 0;
  position: relative;
  background-image: url("../img/background-catalog-advertise.jpg");
  background-color: #68b738;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-align: center;
  color: #ffffff;
  min-height: 290px;
  box-sizing: border-box;
}
@media (min-resolution: 2dppx) {
  .additional-products__advertising-text {
    background-image: url("../img/background-catalog-advertise@2x.jpg");
  }
}
.additional-products__advertising-text::before {
  position: absolute;
  content: url("../img/icon-gift.svg");
}
@media (max-width: 767px) {
  .additional-products__advertising-text::before {
    top: 59px;
    left: 101px;
    right: 101px;
  }
}
@media (min-width: 768px) {
  .additional-products__advertising-text::before {
    top: 57px;
    left: 77px;
  }
}
@media (min-width: 1300px) {
  .additional-products__advertising-text::before {
    top: 56px;
    left: 82px;
    right: 82px;
  }
}
@media (max-width: 767px) {
  .additional-products__advertising-text {
    padding: 187px 61px 43px 58px;
  }
}
@media (min-width: 768px) {
  .additional-products__advertising-text {
    background-image: url("../img/background-catalog-advertise-tablet.jpg");
    min-height: 200px;
    padding: 71px 125px 69px 421px;
    text-align: left;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .additional-products__advertising-text {
    background-image: url("../img/background-catalog-advertise-tablet@2x.jpg");
  }
}
@media (min-width: 1300px) {
  .additional-products__advertising-text {
    background-image: url("../img/background-catalog-advertise-desctop.jpg");
    min-height: 288px;
    width: 245px;
    padding: 185px 40px 43px;
    text-align: center;
  }
}
@media (min-width: 1300px) and (min-resolution: 2dppx) {
  .additional-products__advertising-text {
    background-image: url("../img/background-catalog-advertise-desctop@2x.jpg");
  }
}
.additional-products__list {
  margin: 0;
  padding: 0;
  list-style: none;
  flex-grow: 1;
}
@media (min-width: 1300px) {
  .additional-products__list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 896px;
  }
}
.advantages {
  background-color: #ffffff;
}
.advantages .advantages__container {
  padding-bottom: 29px;
}
@media (min-width: 768px) {
  .advantages .advantages__container {
    padding-bottom: 26px;
  }
}
@media (min-width: 1300px) {
  .advantages .advantages__container {
    padding-bottom: 50px;
  }
}
.advantages__title {
  font-family: "Oswald", Tahoma, sans-serif;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  color: #000000;
  margin: 0;
  margin-bottom: 39px;
}
@media (min-width: 768px) {
  .advantages__title {
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 32px;
  }
}
@media (min-width: 1300px) {
  .advantages__title {
    margin-bottom: 32px;
  }
}
.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr;
  counter-reset: advantage-number;
}
@media (min-width: 768px) {
  .advantages__list {
    grid-template-columns: 245px 245px;
    column-gap: 138px;
    row-gap: 17px;
  }
}
@media (min-width: 1300px) {
  .advantages__list {
    grid-template-columns: 244px 245px 245px 250px;
    column-gap: 80px;
  }
}
.advantages-card {
  position: relative;
  font-weight: 400;
  color: #444444;
  box-sizing: border-box;
  padding-left: 80px;
  background-position: left top;
  background-repeat: no-repeat;
  background-size: 60px 60px;
  margin-bottom: 20px;
  counter-increment: advantage-number;
  z-index: 0;
}
@media (max-width: 767px) {
  .advantages-card {
    min-height: 60px;
    padding-top: 3px;
  }
  .advantages-card--product-composition {
    background-image: url("../img/advantages/product-composition.svg");
  }
  .advantages-card--eating {
    background-image: url("../img/advantages/eating.svg");
  }
  .advantages-card--meals-frequency {
    background-image: url("../img/advantages/meals-frequency.svg");
  }
  .advantages-card--eating-result {
    background-image: url("../img/advantages/eating-result.svg");
  }
}
@media (min-width: 768px) {
  .advantages-card {
    min-height: 280px;
    background-size: 80px 80px;
    background-position: left top 40px;
    padding: 151px 0 57px 0;
  }
  .advantages-card::after {
    display: block;
    position: absolute;
    z-index: -1;
    content: counter(advantage-number);
    left: 116px;
    top: 0;
    color: #f2f2f2;
    font-family: "Oswald", Tahoma, sans-serif;
    font-size: 280px;
    line-height: 280px;
  }
  .advantages-card--product-composition {
    background-image: url("../img/advantages/product-composition-tablet.svg");
  }
  .advantages-card--eating {
    background-image: url("../img/advantages/eating-tablet.svg");
  }
  .advantages-card--meals-frequency {
    background-image: url("../img/advantages/meals-frequency-tablet.svg");
  }
  .advantages-card--eating-result {
    background-image: url("../img/advantages/eating-result-tablet.svg");
  }
}
.advantages-card__inner {
  margin: 0;
}
@media (min-width: 768px) {
  .advantages-card__inner {
    font-size: 16px;
    line-height: 24px;
  }
}
.button {
  border: none;
  font-family: "Oswald", Tahoma, sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}
.button--primary {
