  }
}
@media (min-width: 1300px) {
  .example__slider {
    position: absolute;
    top: -135px;
    right: 40px;
  }
}
.example__slider-control {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.example__slider-button {
  display: block;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 2px solid #666666;
  position: relative;
}
.example__slider-button:hover {
  border: 2px solid #68b738;
}
.example__slider-button:active {
  border: 2px solid #5eaa2f;
}
.example__slider-button::before {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "";
  width: 24px;
  height: 18px;
  mask-image: url("../img/sprite.svg#slider-control-icon");
  mask-position: center;
  background-color: #666666;
  z-index: 1;
}
.example__slider-button:hover::before {
  background-color: #68b738;
}
.example__slider-button:active::before {
  background-color: #5eaa2f;
}
.example__slider-button::after {
  display: block;
  content: "";
  position: absolute;
  width: 4px;
  background-color: #666666;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
}
@media (max-width: 767px) {
  .example__slider-button::after {
    height: 256px;
  }
}
@media (min-width: 768px) {
  .example__slider-button::after {
    height: 512px;
  }
}
.example__slider-button:hover::after {
  background-color: #68b738;
